@font-face {
  font-family: "PTSans";
  font-weight: 400;
  font-style: normal;
  font-display: swap;
  src: local("PTSans Regular"), url("../fonts/ptsans.woff2") format("woff2"), url("../fonts/ptsans.woff") format("woff");
}
@font-face {
  font-family: "PTSans";
  font-weight: 700;
  font-style: normal;
  font-display: swap;
  src: local("PTSans Bold"), url("../fonts/ptsansbold.woff2") format("woff2"), url("../fonts/ptsansbold.woff") format("woff");
}
@font-face {
  font-family: "Exo 2";
  font-weight: 600;
  font-style: normal;
  font-display: swap;
  src: local("Exo 2 SemiBold"), url("../fonts/exo2semibold.woff2") format("woff2"), url("../fonts/exo2semibold.woff") format("woff");
}
body {
  min-width: 320px;
  margin: 0;
}
img {
  max-width: 100%;
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}
.page-body {
  font-family: "PT Sans", "Arial", sans-serif;
  font-size: 14px;
  font-weight: 400;
  color: #000000;
  margin: 0 auto;
  position: relative;
  min-height: 100%;
}
.container {
  margin: 0 auto;
}
@media (min-width: 768px) {
  .container {
    width: 708px;
    margin: 0 auto;
    padding: 0 30px;
  }
}
@media (min-width: 1440px) {
  .container {
    width: 1220px;
    margin: 0 auto;
    padding: 0 40px;
  }
}
.page-header__wrapper {
  padding: 0 16px;
  display: flex;
  align-items: center;
  flex-direction: column;
  z-index: 1;
  background-color: #0B7572;
}
.page-header__container {
  padding: 75px 0 181px;
}
.page-header__title {
  font-family: "Exo 2", "Arial", sans-serif;
  font-weight: 600;
  font-size: 26px;
  line-height: 35px;
  color: #ffffff;
  margin: 0;
}
.page-header__slogan {
  font-family: "PT Sans", "Arial", sans-serif;
  font-weight: 400;
  font-size: 15px;
  line-height: 23px;
  color: #ffffff;
  text-align: left;
}
.page-header__slogan br {
  display: none;
}
.page-header__slogan-none {
  display: none;
}
.page-header__link {
  font-family: "PT Sans", "Arial", sans-serif;
  font-weight: 700;
  font-size: 15px;
  line-height: 23px;
  color: #ffffff;
  text-decoration: none;
  position: relative;
}
.page-header__link::after {
  content: "";
  position: absolute;
  left: 10px;
  top: 10px;
  width: 99px;
  height: 0;
  background-color: #FFCE50;
}
.page-header__link:hover {
  color: #6B6B6C;
}
.page-header__image {
  width: 345px;
  height: 253px;
  background-color: #6B6B6C;
}
.page-main__wrapper {
  padding: 0 16px;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}
.page-main__list {
  background-color: #ffffff;
  padding: 0;
  margin: 0;
  list-style: none;
  box-shadow: 0 4px 20px rgba(0, 0, 0, 0.15);
  border-radius: 20px;
  position: relative;
  top: -20px;
}
.page-main__item {
  padding: 24px 27px 24px 32px;
  margin: 0;
}
.page-main__item--goods svg,
.page-main__item--delivery svg,
.page-main__item--manufacturer svg {
  width: 68px;
  height: 68px;
}
.page-main__title {
  font-family: "Exo 2", "Arial", sans-serif;
  font-weight: 600;
  font-style: normal;
  font-size: 18px;
  line-height: 24px;
  color: #363738;
  margin: 16px 0 12px 0;
}
.page-main__slogan {
  font-family: "PT Sans", "Arial", sans-serif;
  font-weight: 400;
  font-size: 15px;
  line-height: 23px;
  color: #6B6B6C;
  text-align: left;
  margin-bottom: 0;
}
.scope-work__container {
  padding: 60px 0;
}
.scope-work__lead {
  font-family: "Exo 2", "Arial", sans-serif;
  font-weight: 600;
  font-size: 28px;
  line-height: 35px;
  color: #363738;
  margin: 0;
}
.scope-work__slogan {
  display: none;
}
.scope-work__list {
  list-style: none;
  margin: 0;
  padding: 32px 0 0 0;
}
.scope-work__item {
  padding: 24px 27px 32px 16px;
  box-shadow: 0 3px 15px rgba(0, 0, 0, 0.15);
  border-radius: 10px;
  margin-bottom: 10px;
  display: flex;
  gap: 10px;
}
.scope-work__item--checked {
  border: 1px solid #0B7572;
}
.scope-work__title {
  font-family: "Exo 2", "Arial", sans-serif;
  font-weight: 600;
  font-size: 18px;
  margin: 0;
}
.scope-work__text {
  font-family: "PT Sans", "Arial", sans-serif;
  font-weight: 400;
  font-size: 14px;
  line-height: 21px;
  color: #6B6B6C;
  margin: 10px 0 0 0;
}
.scope-work__checkmark {
  position: absolute;
  width: 24px;
  height: 24px;
  background-image: url("../img/checkbox.svg");
  background-repeat: no-repeat;
}
.scope-work__box {
  display: none;
}
